.header-image {
  max-height: 60vh;
  overflow: hidden;

  img {
    display: block;
    width: 100vw;
  }
}

.site-banner {
  position: relative;
  background-color: $color__banner;
}


.site-header {
  position: relative;
  padding: 1em;
  font-family: $font__sans;
  color: white;
  background-color: $color__skin;

  @media screen and (min-width: $query__small) {
    padding: 1em 2em;
  }
}

.site-branding {
  display: flex;
  min-height: 65px;
}

.custom-logo-link {
  margin-right: 1em;
  img {
    display: block;
    height: 70px;
    width: auto;
  }
}

.custom-logo {
  margin: 0;
  padding: 0;
}

.site-branding__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 50px;
}

.site-title {
  margin: 0;
  padding: 0;
  font-size: 2em;
  font-weight: 900;
  line-height: 1em;
  font-family: $font__sans;

  a {
    color: white;
    text-decoration: none;

    &:hover,
    &:focus {
      color: $color__interactive;
    }
  }
}

.site-description {
  margin: 0;
  font-size: 1.5em;
  font-family: $font__tagline;
  font-weight: 100;
  position: absolute;
  top:60px;
}

@media screen and (min-width: $query__medium) {
  .site-header {
    display: flex;
    justify-content: space-between;
  }
  .site-branding {
    width: 35%;
    flex: 1 0 auto;
  }
  .site-navigation {
    width: 55%;
  }
}

.social-menu__top {

  ul {
    display: flex;
    justify-content: flex-end;
    list-style-type: none;
    margin: 0;
    padding: 0;
  }

  a {
    display: inline-block;
    padding: .5em 1em;
    color: $color__banner__bottom;
    text-decoration: none;
    opacity: .6;

    &:hover,
    &:focus {
      color: $color__interactive;
      opacity: 1;
    }
  }

}

/* Header and media menu
*/
@import "header-menu";
@import "media-menu";